<template>
  <div id="box">
    <router-link to="/user_info" class="head flex-between">
      <div class="left flex-center-y">
        <img :src="data.head_pic" />
        <p>{{data.nickname}}</p>
        <van-icon name="arrow" class="arrow" />
      </div>
    </router-link>
    <div class="wrap">
      <router-link to="/setPhone?isshow=1" class="item flex-between">
        <div class="title">实名认证</div>
        <van-icon name="arrow" class="arrow" />
      </router-link>
      <a class="item flex-between">
        <div class="title">支付密码</div>
        <van-icon name="arrow" class="arrow" />
      </a>
      <router-link to="/setPhone?isshow=0" class="item flex-between">
        <div class="title">绑定手机号码</div>
        <van-icon name="arrow" class="arrow" />
      </router-link>
    </div>
  </div>
</template>

<script>
import { Icon, NavBar } from "vant";
import axios from "axios";
import url from "@/axios/api.js";
export default {
  data() {
    return {
      data: [],
      To: "/setPhonePwd?isshow=1"
    };
  },
  components: {
    "van-icon": Icon,
    vanNavBar: NavBar
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      let params = new URLSearchParams();

      params.append("openid", localStorage.getItem("openid"));
      axios.post(url.userInfo, params).then(res => {
        this.data = res.data.data;
      });
    }
  }
};
</script>

<style scoped lang="scss">
.head {
  .left {
    display: flex;
    align-items: center;
    padding: 10px;
    background: #fff;
    img {
      width: 53px;
      height: 53px;
      border-radius: 50%;
    }
    p {
      margin-left: 20px;
      font-size: 16px;
      color: rgba(79, 79, 79, 1);
    }
    i {
      margin-left: auto;
    }
  }
}
.arrow {
  color: #8b8682;
}
.wrap {
  background: #fff;
  padding: 0 10px;
  margin-top: 10px;
  .item {
    display: flex;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid #ebebeb;
    &:last-child {
      border-bottom: 0;
    }
    .title {
      font-size: 16px;
      color: rgba(79, 79, 79, 1);
    }
    i {
      margin-left: auto;
    }
    .right {
      span {
        color: #959595;
        font-size: 26px;
        margin-right: 6px;
      }
    }
  }
}
.loginOut {
  height: 100px;
  position: fixed;
  background: #fff;
  width: 100%;
  bottom: 0;
  color: #ee241e;
  font-size: 34px;
  box-shadow: 0 0 14px #eee;
}
</style>



